
import XImg from 'vux/src/components/x-img/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

import {getParams,weChatPay} from '../../assets/js/util'
import api from '../../assets/js/api'
export default {
directives: {
TransferDom
},
components: {
XImg,
CheckIcon,
Popup
},
data () {
return {
demo1: true,
bottomCount: 2,
showChoice: false,
isPost: getParams()['delivery'] == 2 ? false : true,
order_id: getParams()['order_id'] || '',
detail: {}
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(text){
this.$vux.loading.show({
text: text || '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
setRecvier(){
if (this.isPost) {
this.$router.push({
path: `/address?order_id=${this.detail.ordersn}`
})
} else {
this.$router.push({
path: `/selfGet?order_id=${this.detail.ordersn}`
})
}
},
log(start){
console.log(start)
},
gotoOnlinePay(){
this.settlementOrder(2)
},
gotoOffinePay(){
this.settlementOrder(1)
},
/* 获取确认订单的信息 */
getOrderData(){
this.showLoading('获取中')
this.$http.post(api.confirmOrder,{
ordersn: this.order_id
}).then(res => {
this.hideLoading()
this.detail = res.data
}).catch(e => {
this.hideLoading()
})
},
/* 结算支付 */
settlementOrder(payType){
this.showChoice = false
this.showLoading('提交中')
let data = {}, delivery = 1;
if( this.isPost ){
if( !this.detail.post_address ){
return this.layer('请选择邮寄地址')
}
data = {
ordersn: this.detail.ordersn,
delivery: 1,
username: this.detail.post_address.username,
phone: this.detail.post_address.phone,
province: this.detail.post_address.province,
city: this.detail.post_address.city,
district: this.detail.post_address.district,
address: this.detail.post_address.address,
payType: payType
}
}else{
delivery = 2;
if( !this.detail.self_address ){
return this.layer('请填写提货信息')
}
data = {
ordersn: this.detail.ordersn,
delivery: 2,
username: this.detail.self_address.username,
phone: this.detail.self_address.phone,
province: '',
city: '',
district: '',
address: '',
payType: payType
}
}

this.$http.post(api.settlementOrder,data).then(res => {
this.hideLoading()
/*1是线下支付，2是线上支付*/
if( payType == 1 ){
/*线下支付*/
this.$router.push({
path: `/offinePay?order_id=${this.detail.ordersn}&delivery=${delivery}`
})
}else{
/*线上支付*/
if(cookie.get('my_openId')){
this.$router.push({
path: `/weChatPay?order_id=${this.detail.ordersn}&delivery=${delivery}`
})
}else{
location.href = weChatPay(this.detail.ordersn,delivery)
}
}
}).catch(e => {
this.hideLoading()
})
}
},
mounted(){
this.getOrderData();
}
}



