
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import XInput from 'vux/src/components/x-input/index.vue'
import value2name from 'vux/src/filters/value2name.js'

import api from '../../assets/js/api'
import {getParams} from '../../assets/js/util'
export default {
components: {
XAddress,
XInput
},

data () {
return {
addressData: ChinaAddressV4Data,
showAddress: false,
name: '',
phone: '',
addressDetail: '',
value: ['北京市', '市辖区', '朝阳区'],
type: getParams()['type'] || 'add',
address_id: getParams()['id'] || ''
}
},
computed: {
address() {
return value2name(this.value, ChinaAddressV4Data)
},
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(text){
this.$vux.loading.show({
text: text || '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
editAddress(){
if (!this.$refs.nameInput.valid) {
this.layer('请输入收货人')
} else if (!this.$refs.phoneInput.valid) {
this.layer('请输入联系电话')
} else if (!this.$refs.addressDetailInput.valid) {
this.layer('请输入详细地址')
} else {
let area = this.address.split(" ")
let data = {
name: this.name,
phone: this.phone,
address: this.addressDetail,
province: area[0],
city: area[1],
district: area[2]
}
if (this.type === 'edit') {
data.address_id = this.address_id
}
this.showLoading('正在添加');
this.$http.post(api.addressEditConfirm, data)
.then(res => {
this.hideLoading()
this.layer('添加成功')
this.$router.go(-1)
}).catch(e => {
this.hideLoading()
})
}
},
getAddress(){
this.showLoading()
this.$http.post(api.addressGet, {
address_id: this.address_id
}).then(res => {
this.hideLoading()
this.name = res.data.username
this.phone = res.data.phone
this.addressDetail = res.data.address
this.value = [res.data.province, res.data.city, res.data.district]
}).catch(e => {
this.hideLoading()
})
}
},
mounted(){

document.getElementsByTagName('title')[0].textContent = '添加地址';
this.address_id && this.getAddress();
}
}



