
import Search from 'vux/src/components/search/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'

import InfiniteLoading from 'vue-infinite-loading'
import Nodata from '../../components/nodata.vue'
import api from '../../assets/js/api'
export default {
components: {
Search,
Tab,
TabItem,
XImg,
InlineXNumber,
InfiniteLoading,
Nodata
},
data () {
return {
search: '',
onFetching: false,
bottomCount: 0,
changeValue: 1,
value: 1,
type: 'all',
types: ['all', 'discount', 'count'],
all: {
page: 1,
data: []
},
discount: {
page: 1,
data: []
},
count: {
page: 1,
data: []
},
shoppingTrolley: 0,


}
},
computed: {
goods(){
return this[this.type].data;
},
total(){
let allMoney = 0,
discountMoney = 0,
countMoney = 0,
allReMoney = 0,
discountReMoney = 0,
countReMoney = 0,
allNumber = 0,
discountNumber = 0,
countNumber = 0,
checkedGoods = [];
this.all.data.forEach(val => {
if (val.number > 0) {
allMoney += val.good_price * val.number;
allReMoney += val.rebate * val.number;
allNumber += val.number;
checkedGoods.push({
good_id: val.id,
amount: val.number
})
}
})
this.discount.data.forEach(val => {
if (val.number > 0) {
discountMoney += val.good_price * val.number
discountReMoney += val.rebate * val.number
discountNumber += val.number;
checkedGoods.push({
good_id: val.id,
amount: val.number
})
}
})
this.count.data.forEach(val => {
if (val.number > 0) {
countMoney += val.good_price * val.number
countReMoney += val.rebate * val.number
countNumber += val.number;
checkedGoods.push({
good_id: val.id,
amount: val.number
})
}
})
return {
totalMoney: (allMoney + discountMoney + countMoney) || 0,
totalNumber: (allNumber + discountNumber + countNumber) || 0,
totalReMoney: (allReMoney + discountReMoney + countReMoney) || 0
};
},
totalRebate(){

}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(text){
this.$vux.loading.show({
text: text || '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
onSubmit () {

this.$refs.search.setBlur()
this.resetData();
},
resetData(){
this.all.page = 1;
this.all.data = [];
this.discount.page = 1;
this.discount.data = [];
this.count.page = 1;
this.count.data = [];
this.$nextTick(() => {
this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset');
})
},
getResult(){
this.resetData();
},
getGoods ($state) {

this.$http.post(api.getGoods, {
type: this.type,
page: this[this.type].page,
name: this.search
}).then(res => {
if (res.data.length) {
res.data.forEach(val => {
val.number = 0
})
this[this.type].data = this[this.type].data.concat(res.data);
this[this.type].page++;
this.shoppingTrolley = res.data.shoppingTrolley;
$state.loaded();
} else {
$state.complete();
}
}).catch(e => {
$state.complete();
})
},
change(data){
console.log(data)
},
onItemClick(index){
this.type = this.types[index]
this.$nextTick(() => {
this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset');
})
},
gotoShopCart(){
let checkedGoods = [];
this.all.data.forEach(val => {
if (val.number > 0) {
checkedGoods.push({
good_id: val.id,
amount: val.number
})
}
})
this.discount.data.forEach(val => {
if (val.number > 0) {
checkedGoods.push({
good_id: val.id,
amount: val.number
})
}
})
this.count.data.forEach(val => {
if (val.number > 0) {
checkedGoods.push({
good_id: val.id,
amount: val.number
})
}
})
if (checkedGoods.length <= 0) {
return this.layer('请选择商品')
}
this.showLoading('正在添加')
this.$http.post(api.shoppingTrolleyAdd, {
goodList: JSON.stringify(checkedGoods)
}).then(res => {
this.hideLoading()
this.$router.push({
path: '/shopCart'
})
}).catch(e => {
this.hideLoading()
})
}
},
mounted(){

document.getElementsByTagName('title')[0].textContent = '内购商城';
}
}



