
import CheckIcon from 'vux/src/components/check-icon/index.vue'

import api from '../../assets/js/api'
import Nodata  from '../../components/nodata.vue'
import {getParams} from '../../assets/js/util'
export default {
components: {
CheckIcon,
Nodata
},
data () {
return {
demo1: false,
bottomCount: 2,
isEdit: false,
list: [],
order_id: getParams()['order_id'] || ''
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(text){
this.$vux.loading.show({
text: text || '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
deleteAddress(id){
const _this = this;
this.$vux.confirm.show({

content: '删除后不可恢复，确认删除？',

onCancel () {
console.log(this) 
console.log(_this) 
},
onConfirm () {
_this.operateAddress(id,'delete')
}
})
},
setMainAddress(id){
const _this = this;
this.$vux.confirm.show({

content: '确定设置为默认地址？',

onCancel () {
console.log(this) 
console.log(_this) 
},
onConfirm () {
_this.operateAddress(id,'modifyDefault')
}
})
},
gotoEditAddress(id){
this.$router.push({
path: `/addAddress?type=edit&id=${id}`
})
},
gotoAddAddress(){
this.$router.push({
path: '/addAddress?type=add'
})
},

operateAddress(id,type){
this.showLoading('修改中')
this.$http.post(api.operateAddress,{
address_id: id,
type: type
}).then(res => {
this.hideLoading()
this.layer('修改成功')
if( type == 'modifyDefault'){
this.$router.push({
path: `/pay?order_id=${this.order_id}`
})
}else{
this.getAddressList()
}
}).catch(e => {
this.hideLoading()
})
},

getAddressList(){
this.showLoading('正在获取')
this.$http.post(api.addressManage)
.then(res => {
this.hideLoading();
this.list = res.data;
}).catch(e => {
this.hideLoading();
})
}
},
mounted(){
this.getAddressList();

document.getElementsByTagName('title')[0].textContent = '收货地址管理';
}
}



