
import XInput from 'vux/src/components/x-input/index.vue'

import api from '../../assets/js/api'
import {getParams} from '../../assets/js/util'
export default {
components: {
XInput
},
data () {
return {
name: '',
phone: '',
order_id: getParams()['order_id'] || ''
}
},
computed: {},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(text){
this.$vux.loading.show({
text: text || '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
saveInformaton(){
if (!this.$refs.nameInput.valid) {
this.layer('请输入收货人')
} else if (!this.$refs.phoneInput.valid) {
this.layer('请输入手机号码')
} else {
this.showLoading('正在保存')

this.$http.post(api.editSelfAddress,{
username: this.name,
phone: this.phone
}).then(res => {
this.hideLoading()
this.layer('修改成功')
this.$router.push({
path: `/pay?order_id=${this.order_id}&delivery=2`
})
}).catch(e => {
this.hideLoading()
})
}
},
/*获取收货人信息*/
getSelfAddress(){
this.showLoading('获取中')
this.$http.post(api.getSelfAddress)
.then(res => {
this.hideLoading()
this.name = res.data.username
this.phone = res.data.phone
}).catch(e => {
this.hideLoading()
})
}
},
mounted(){
this.getSelfAddress()

document.getElementsByTagName('title')[0].textContent = '自提设置';
}
}



