
import XImg from 'vux/src/components/x-img/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

import InfiniteLoading from 'vue-infinite-loading';
import api from '../../assets/js/api'
import Nodata from '../../components/nodata.vue'
export default {
components: {
XImg,
InlineXNumber,
CheckIcon,
InfiniteLoading,
Nodata
},
data () {
return {
bottomCount: 0,
changeValue: 1,
value: 1,
demo1: true,
isEdit: false,          // 是否处于编辑状态
goods: [],
count: 0,
page: 1,
}
},
computed: {
total(){
let allMoney = 0,
allReMoney = 0,
allNumber = 0;
this.goods.forEach(val => {
if (val.amount > 0 && val.checked) {
allMoney += val.good_price * val.amount;
allReMoney += val.rebate * val.amount;
allNumber += val.amount;
}
})
return {
totalMoney: allMoney || 0,
totalNumber: (allNumber + this.count) || 0,
totalReMoney: allReMoney || 0
};
},
checkedGood(){
let deleteArr = [],
editArr = [];
this.goods.forEach(val => {
if (val.checked) {
deleteArr.push({
good_id: val.good_id
});
editArr.push({
good_id: val.good_id,
amount: val.amount
});
}
});
return {
deleteArr,
editArr
}
}
},
watch: {
demo1(curval){
this.goods.forEach(val => {
val.checked = curval
});
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(text){
this.$vux.loading.show({
text: text || '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
getGoods ($state) {
this.$http.post(api.shoppingTrolley,{
page: this.page
}).then(res => {

if (res.data.list.length) {
res.data.list.forEach(val => {
val.checked = true
})
this.page++
this.goods = this.goods.concat(res.data.list)
$state.loaded();
} else {
$state.complete();
}
}).catch(e => {
$state.complete();
})
},

deleteGoods(){
let editArr = [];
this.goods.forEach(val => {
if (val.checked) {
editArr.push({
good_id: val.good_id,
amount: val.amount
});
}
});
this.showLoading('删除中')
this.$http.post(api.shoppingTrolleyEdit, {
type: 'delete',
goodList: JSON.stringify(editArr)
}).then(res => {
this.hideLoading();
this.layer('删除成功')
this.resData()
}).catch(e => {
this.hideLoading();
})
},
resData(){
this.page = 1;
this.goods = [];
this.$nextTick(() => {
this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset');
})
},
editGoods(){
let editArr = [];
this.goods.forEach(val => {
if (val.checked) {
editArr.push({
good_id: val.good_id,
amount: val.amount
});
}
});
this.showLoading('提交中')
this.$http.post(api.shoppingTrolleyEdit, {
type: 'modify',
goodList: JSON.stringify(editArr)
}).then(res => {
this.isEdit = false;
this.hideLoading();
this.layer('编辑成功')
this.resData()
}).catch(e => {
this.hideLoading();
})
},
allChecked(){
this.goods.forEach(val => {
if (this.demo1) {
val.checked = true;
} else {
val.checked = false;
}
})
},
submitOrder(){
let addGoodsArr = [];
this.goods.forEach(val => {
if (val.checked) {
addGoodsArr.push({
good_id: val.good_id,
amount: val.amount
});
}
});
if(!addGoodsArr.length){
this.layer('清先选择商品')
return this.$router.push({
path: '/market'
})
}
this.showLoading('加载中')
this.$http.post(api.addOrder,{
goodList: JSON.stringify(addGoodsArr)
}).then(res => {
this.hideLoading()
this.$router.push({
path: `/pay?order_id=${res.data.ordersn}`
})
}).catch(e => {
this.hideLoading()
})
},
},
mounted(){

document.getElementsByTagName('title')[0].textContent = '购物车';
}
}



