
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Scroller from 'vux/src/components/scroller/index.vue'

import api from '../../assets/js/api'
import {getParams} from '../../assets/js/util'

export default {
directives: {
TransferDom
},
components: {
Previewer,
Scroller
},
data () {
return {
isShowDesc: false,
isShowZh: false,
desc: '',
amount: '',
order_id : getParams()['order_id'] || '',
list: [],
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(){
this.$vux.loading.show({
text: '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
show (index) {
this.$refs.previewer.show(index)
},
getVoucher(){
this.$http.post(api.getVoucher,{
ordersn: this.order_id
}).then(res => {
this.amount = res.data.money
res.data.photo.forEach(val => {
this.list.push({
msrc: val,
src: val
})
})
this.desc = res.data.remark

}).catch(e => {

})
}
},
mounted() {
this.getVoucher()
document.getElementsByTagName('title')[0].textContent = '订单凭证';
}
}



