
import XInput from 'vux/src/components/x-input/index.vue'

import {getParams} from '../../assets/js/util'
import api from '../../assets/js/api'

export default {
components: {
XInput
},
data () {
return {
isShowDesc: false,
isShowZh: false,
order_id: getParams()['order_id'] || '',
delivery: getParams()['delivery'] || '',   //  1  邮寄   2 自提
detail: {}
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(){
this.$vux.loading.show({
text: '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
gotoUpload(){
this.$router.push({
path: `/upload?order_id=${this.order_id}`
})
},
gotoOrderDetail(){
this.$router.push({
path: `/orderDetail?order_id=${this.order_id}`
})
},
getAccount(){
this.$http.post(api.orderAccount,{
ordersn: this.order_id
}).then(res => {
this.detail = res.data
})
}
},
mounted() {
this.getAccount()
document.getElementsByTagName('title')[0].textContent = '下单成功';
}
}



